import React, {
  useState,
  useEffect,
  useCallback
} from 'react';
import {
  Box,
  Container,
  makeStyles
} from '@material-ui/core';
import Page from 'src/components/Page';
import useIsMountedRef from 'src/hooks/useIsMountedRef';
import Header from './Header';
import Results from './Results';
import { listH5pByUser } from '../../../api/h5p';
import useAuth from 'src/hooks/useAuth';


const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  }
}));

const LivestreamListView = () => {
  const classes = useStyles();
  const { user} = useAuth();
  const isMountedRef = useIsMountedRef();
  const [h5ps, setH5ps] = useState([]);

  
  const getH5ps = useCallback(async () => {

      console.log(user)
    try {
      const h5ps = await listH5pByUser(user.userId);
      if (isMountedRef.current) {
        setH5ps(h5ps);
      }
    } catch (err) {
      console.error(err);
    }
  }, [isMountedRef]);
  
  useEffect(() => {getH5ps();}, [getH5ps]);
  

  return (
    <Page
      className={classes.root}
      title="Trường học"
    >
      <Container maxWidth={false}>
        <Header/>
        <Box mt={3}>
          <Results h5ps={h5ps}/>
        </Box>
      </Container>
    </Page>
  );
};

export default LivestreamListView;
